import "./App.css";
import { HomePage } from "./pages/HomePage";
import { HeaderMenuInjector } from "./components/HeaderMenuInjector";
import { Container, Segment } from "semantic-ui-react";
import { Main } from "./styles/Main";
import { Footer } from "./styles/Footer";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import { ContactPage } from "./pages/ContactPage";
import { PreparingPage } from "./pages/PreparingPage";
import { HamsterRunPage } from "./pages/HamsterRunPage";
import { AboutClaritySolutionsPage } from "./pages/AboutClaritySolutionsPage";
import { ShareNinjaPage } from "./pages/ShareNinjaPage";
import { DDDOGPage } from "./pages/DDDOGPage";
import { HowToInstallDddogBetaAppPage } from "./pages/HowToInstallDDDOGBetaAppPage";

function App() {
  return (
    <div className="App">
      <Router hashType="slash">
        <HeaderMenuInjector>
          <Main>
            <Switch>
              <Route exact path="/">
                <HomePage />
              </Route>
              <Route path="/about_us">
                <AboutClaritySolutionsPage />
              </Route>
              <Route path="/contact">
                <ContactPage />
              </Route>
              <Route exact path="/products/dddog">
                <DDDOGPage />
              </Route>
              <Route path="/products/dddog/how_to_install_beta_app">
                <HowToInstallDddogBetaAppPage />
              </Route>
              <Route path="/products/hamster_run">
                <HamsterRunPage />
              </Route>
              <Route path="/products/share_ninja">
                <ShareNinjaPage />
              </Route>
              <Route path="*">
                <PreparingPage />
              </Route>
            </Switch>
          </Main>
          <Footer>
            <Segment fixed="bottom">
              <Container>© Clarity Solutions, LLC</Container>
            </Segment>
          </Footer>
        </HeaderMenuInjector>
      </Router>
    </div>
  );
}

export default App;
